<h1 id="android-listview" tabindex="-1"><a class="header-anchor" href="#android-listview" aria-hidden="true">#</a> Android ListView</h1>
<p>It's very common for an application to display a list of items. But if there are many items in the list, how can we do that in a resource and speed efficient way? Android's solution to that was initially the <code>ListView</code>, which we'll take a closer look at in this lecture. Nowadays the Android Jetpack libraries also has a <code>RecyclerView</code> we can use to achieve similar functionality. That one is a bit more configurable, but also a bit harder to use.</p>
